.welcome {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 100%;
  height: 100vh;
  padding: 70px 0 0 0;
  background-position: right bottom;
  background-repeat: no-repeat;
  background-size: 90vh 90vh;
  overflow: hidden;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.welcome-greeting {
  margin-left: 8vw;
  width: 40vw;
  margin-top: 20vh;
  font-size: 40px;
  font-weight: 300;
  letter-spacing: 2px;
  cursor: default;
}

.welcome-background {
  background-position: right;
  background-repeat: no-repeat;
  background-size: 70vh 70vh;
  width: 400px;
  height: 600px;
  margin: 0 0 0 1vw;
  right: 0;
  -ms-flex-item-align: baseline;
      align-self: baseline;
}

.welcome-paragraph {
  margin-left: 8vw;
  width: 40vw;
  margin-top: 4vh;
  font-size: 16px;
  font-weight: 300;
  letter-spacing: 1px;
  line-height: 26px;
  cursor: default;
}

.black-fade {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-item-align: baseline;
      align-self: baseline;
  width: 100%;
  background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#07070a));
  background: -webkit-linear-gradient(transparent, #07070a);
  background: -o-linear-gradient(transparent, #07070a);
  background: linear-gradient(transparent, #07070a)
}


